@import url(app.css);

.user-section {
  width: 80vw;
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.user-container {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 1.5rem;
}

h3 {
  font-size: 2rem;
  text-align: center;
}

table {
  width: 100%;
  display: block;
  overflow-x: auto;
  white-space: nowrap;
}

th {
  text-align: center;
  font-weight: 700;
  padding: 0.3rem 1rem;
  background-color: rgb(219, 223, 246);
  color: var(--bold-text-color);
}

tr {
  padding-bottom: 10px;
}

tr:nth-child(2n + 1) {
  background-color: rgb(234, 234, 234);
}

tr:nth-child(2n + 2) {
  background-color: rgb(226, 237, 255);
}

td {
  padding: 10px;
  color: var(--light-text-color);
  text-align: center !important;
}

.user-table-pic {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid rgb(165, 165, 165);
}

.select {
  display: flex;
  gap: 0.5rem;
  align-items: flex-end;
  justify-content: center;
}

.user-btn {
  background-color: #dc3545 !important; /* Red color for remove buttons */
  color: white !important;
  font-size: 0.8rem;
  padding: 0.5rem 0.8rem;
}

.user-btn:hover {
  background-color: #c82333 !important; /* Darker red on hover */
}

.accept-btn {
  background-color: #28a745 !important; /* Solid green color */
  color: white !important;
  font-size: 0.8rem;
  padding: 0.5rem 0.8rem;
}

.accept-btn:hover {
  background-color: #218838 !important; /* Darker green on hover */
}

@media (max-width: 1126px) { .user-section { width: 70vw; } }
@media (max-width: 875px) { .user-section { width: 65vw; } }
@media (max-width: 700px) { .user-section { width: 60vw; } }
@media (max-width: 537px) { .user-section { width: 55vw; } }
@media (max-width: 426px) { .user-section { width: 50vw; } }

.status-checkbox {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.status-checkbox input[type="checkbox"] {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

.status-text {
  font-weight: 500;
  color: var(--bold-text-color);
}

/* Button color styles for appointments */

/* Mark Complete button styling for admin appointments */
.pending-btn {
  background-color: #dc3545 !important; /* Light blue color for mark complete */
  color: white !important; /* Dark text for better contrast on light blue */
}

.pending-btn:hover {
  background-color: #dc3545 !important; /* Slightly darker light blue on hover */
}

.completed-btn {
  background-color: #28a745 !important; /* Keep green color for completed */
  color: rgb(255, 255, 255) !important;
}

.completed-btn:hover {
  background-color: #218838 !important; /* Keep darker green on hover */
}

/* Remove the conflicting rule that was overriding accept-btn */
/* .user-btn.accept-btn {
  background-color: inherit !important;
} */

.user-btn.disable-btn {
  background-color: inherit !important;
}

/* Admin Status Styles */
.admin-status {
  padding: 4px 8px;
  border-radius: 4px;
  font-weight: 600;
  font-size: 0.8rem;
  text-transform: uppercase;
}

.admin-status.admin {
  background-color: #28a745;
  color: white;
}

.admin-status.user {
  background-color: #6c757d;
  color: white;
}

/* Admin Management Button Styles */
.promote-btn {
  background-color: #17a2b8 !important;
  color: white !important;
  margin-left: 5px;
}

.promote-btn:hover {
  background-color: #138496 !important;
}

.demote-btn {
  background-color: #ffc107 !important;
  color: #212529 !important;
  margin-left: 5px;
}

.demote-btn:hover {
  background-color: #e0a800 !important;
}

/* Adjust select column width for multiple buttons */
.select {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  min-width: 120px;
}
